li{
    min-width: 280px;
    max-width:280px;
    min-height:73px;
    /* border-radius: 16px; */
    padding: 10px;
    box-shadow:  0 0 5px 0 #7c7c7c;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #3c3d3d;
}

li:hover{
    box-shadow: 0 0 8px 0 #b1b1b1 ;
}

p{
    color: #fff;
    text-align: center;
}

.TodoItem--icon-delete:last-child{
    color: #565656;
    position: absolute;
    top:5px;
    right: 5px;
    background-color: transparent;
    border: none;
}

.TodoItem--icon-delete:hover{
    color: rgb(153, 7, 7);
}

.TodoItem-p--complete{
    text-decoration: line-through;
}

.TodoItem--icon-check{
    background-color: transparent;
    border: none;
    color: #fff;
}

.icon-check--active{
    color: green;
}



