.CreateTodoBtn--btn{
    border-radius: 100%;
    width: 60px;
    height: 60px;
    border: none;
    background-color: rgb(44, 153, 204);
    color: #fff;
    position: relative;
    left:10px;
    bottom: 4px ;
    z-index: 10;
    cursor: pointer;
    box-shadow: 0 0 15px 0 rgb(44, 153, 204) ;
    font-size: 20px;
    
}

.CreateTodoBtn--btn:hover{
    transform: rotate(224deg);
}

