form{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 15px;
    background-color: #252525;
    width: 90%;
    max-width: 300px;
    max-height: 290px;
    margin:0 auto;
    border-radius: 15px;
    padding: 10px ;
    box-shadow: 0 0 0 100vmax rgba(0, 0, 0, 0.5); 
    position: fixed;
    top: 130px;
    left: 0;
    right: 0;
    /* box-shadow:  0 0 0 100vh #818080; */
}

.todo-form-btn--cancel{
    margin-right: 10px;
    /* background-color: #efefef ; */
    
}

.todo-form-btn--cancel, 
.todo-form-btn--add{
    padding: 10px 15px;
    background-color: rgb(44, 153, 204);
    border: none;
    border-radius: 5px;
    color:#fff;
    font-family: poppins ;
    cursor: pointer;
}

textarea{
    border: none;
    border-radius: 8px;
    max-width: 220px;
    min-width: 220px;
    min-height: 150px;
    max-height: 150px;
    padding: 10px;
    font-family: poppins;
    color:#fff;
    background-color: rgba(15, 15, 15, 0.5);
}

textarea:focus{
    outline: 1px solid rgb(44, 153, 204);

}

textarea::placeholder{
    font-family: poppins;
    text-align: center;
    color: #c5c5c2;
}

label{
    color: #fff;
}

