.todos-loading-item{
    width: 300px;
    height: 73px;
    /* border-radius: 16px; */
    padding: 2px;
    /* box-shadow:  0 0 8px 0 #d1cfcf; */
    position: relative;
    /* background-color: #dfdfdf; */
    background: rgb(242,242,242);
    background: linear-gradient(90deg, rgba(242,242,242,0) 0%, rgb(66, 66, 66) 38%, #3c3d3d 76%);
    background-size: 400% 400% ;
    /* opacity: .5; */
    animation: loadingAnimation 3s ease-in-out infinite;
}

@keyframes loadingAnimation {
    0%{
        background-position: 0% 50%;
    }
    50%{
        background-position: 0% 100%;
    }
    100%{
        background-position: 0% 50%;
    }

}